.form__legend {
  background-color: #097969;
  text-align: center;
  color: white;
  margin: 20px 0;
  padding: 15px;
  font-size: 20px;
}

.form__button {
  background-color: #097969;
  border: none;
  color: white;
  width: 100%;
  padding: 10px;
}

